.auth-header{
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 7px 0px;
    font-weight:bold;
}
.auth-header a{
    text-decoration: none;
}

.left {
    flex: 2;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.left-container{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 100%;
    max-width:450px;
}

.logo{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    flex: 1;
    align-items: center;
    justify-content: flex-start;
    align-items: center;
}

.nav{
    flex: 7;
    display: flex;
    align-items: center;
}

.nav ul{
    display: flex;
    flex-direction: row;
    margin: 0px;
    justify-content: space-around;
    padding: 0px;
    text-transform: capitalize;
    color:#402c77;
    width: 100%;
}

.nav ul li{
    list-style-type: none;
}

.right{
    display: flex;
    flex-direction: row;
    flex: 1.4;
    justify-content: flex-end;
}

.right > div{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.right > div > div:nth-child(1){
    width: 60px;
    height: 60px;
    border-radius: 50%;
    margin-right:10px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #f3f3f2;
}

.hambuger{
    display: flex;
    align-items: center;
}


