.wallet-container{
    
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin: 0px 15px 20px 0px;
    color:#432d78;

}

.wallet-container div,span{
    font-weight: 900;
}

.wallet-left-wrapper{
    
    flex: 4;
    margin-right: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;

}

.wallet-left-top{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.card-slider-container{
    
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;

}
.card-slider-container span:nth-child(1){
    cursor: pointer;
}


.card-slider-wrapper {
    width: 100%;
    height: 200px;
    background: teal;
}

.new-card-container{
    
    width: 100%;

}

.form-input-wrapper{
    
    display: flex;
    flex-direction: row;
    justify-content: space-around;

}

.default-card-wrapper {
    
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 20px;

}

.card-button-wrapper{
        display: flex;
        justify-content: center;
        margin: 10px 0px;
}

.activities-container{
    
    width: 100%;

}

.activity-wrapper{
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.activity-card{
    
    display: flex;
    justify-content: space-around;
    padding: 15px;
    border-radius: 10px;
    margin: 10px 0px;
    max-width: 400px;
    width: 400px;
    box-shadow: 7px 4px 8px 0px #949292;
    background: white;
}

.wallet-right-container{
    
    flex: 3;

}

.tab-switch-container{
    
    display: flex;
    justify-content: space-around;
    border-bottom: 1px solid lightgray;

}

.tab-switch-wrapper{
    
    width: 60%;
    display: flex;
    justify-content: space-around;
    position: relative;

}

.selected-item {
    font-size: 20px;
    font-family: Rubik-Bold;
    border: 1px solid lightgray;
    padding: 5px;
    border-bottom: 1px solid white;
    cursor: pointer;
}

.unselected-item {
    display: flex;
    justify-content: center;
    align-items: center;
    color: lightgray;
    cursor: pointer;
}


.select-card-wrapper{
    background: cornflowerblue;
    padding: 10px 0px;
    height: 200px;
}

.pad-content {
    width: 100%;
    padding: 50px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.color-money {
    margin:5px 0px; 
}
.activities-container > h2{
    color:lightgray;
    margin:5px 0px;
}

.pad-right-content{
    
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    /* justify-content: space-between; */

}
.form-wrapper{
    
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-around;

}

.form-wrapper form{
    
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;

}